<template>
  <modal-center title="" @onclose="$emit('getinfo',{type:0})">
    <div v-if="data.binding=='p'">
        <div class="alert alert-danger p-2 m-1">
            <div class="d-flex justify-content-between">
                <div class="bd-highlight"></div>
            </div>
        </div>  
        <div class="p-2">
            <div class="text-center">
                <div><span class="fw-bold fs-4">{{data.fee.passenger}}</span></div>
                <div class="text-black-50"></div>
            </div>
            <div class="rounded-pill px-5" v-if="data.wximg">
                <img :src="data.wximg" width="100%" />
            </div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary" @click="$emit('getinfo',{type:0})"></button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary"  @click="$emit('getinfo',{type:1,data:data.form})"></button>
                </div>
            </div>
        </div>
    </div>
    <div v-else-if="data.binding=='d'">
        <div class="alert alert-danger p-2 m-1">
            <div class="d-flex justify-content-between">
                <div class="bd-highlight"></div>
            </div>
        </div>  
        <div class="text-center p-2">
            <div><span class="fw-bold fs-4">{{data.fee.passenger}}</span></div>
            <div class="text-black-50"></div>
            <div class="text-black-50">:{{data.fee.service}} | {{data.fee.driver}}</div>
        </div>
        <div class="container-fluid p-2">
            <div class="row gx-2">
                <div class="col d-grid">
                    <button class=" btn btn-white border text-primary" @click="$emit('getinfo',{type:0})"></button>
                </div>
                <div class="col d-grid">
                    <button class=" btn btn-primary"  @click="$emit('getinfo',{type:1,data:data.form})"></button>
                </div>
            </div>
        </div>
    </div>
</modal-center>
</template>
<script>
export default {
  props:{
    data:{}
  }
}
</script>

<style>

</style>


